import React from 'react';
import { layoutStyles } from './definitions';

interface LayoutSelectorProps {
    currentLayout: string;
    onChange: (layoutId: string) => void;
}

export function LayoutSelector({ currentLayout, onChange }: LayoutSelectorProps) {
    return (
        <div className="space-y-4">
            <label className="text-sm font-medium text-slate-400">Layout Style</label>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                {layoutStyles.map((item) => {
                    const isActive = currentLayout === item.id;
                    const Icon = item.icon;

                    return (
                        <button
                            key={item.id}
                            type="button"
                            onClick={() => onChange(item.id)}
                            className={`
                group flex flex-col items-center justify-center p-4 rounded-xl border transition-all relative overflow-hidden text-center h-full
                ${isActive
                                    ? 'bg-purple-500/20 border-purple-500 text-white shadow-lg shadow-purple-500/20'
                                    : 'bg-slate-900/40 border-white/5 text-slate-400 hover:bg-white/5 hover:border-white/10'}
              `}
                        >
                            {/* Active Indicator */}
                            {isActive && (
                                <div className="absolute top-2 right-2 w-2 h-2 rounded-full bg-purple-400 shadow-[0_0_10px_rgba(168,85,247,0.8)]" />
                            )}

                            <div className={`
                p-3 rounded-full mb-3 transition-colors
                ${isActive ? 'bg-purple-500 text-white' : 'bg-white/5 text-slate-500 group-hover:bg-white/10 group-hover:text-slate-300'}
              `}>
                                <Icon className="w-5 h-5" />
                            </div>

                            <span className={`text-sm font-semibold mb-1 ${isActive ? 'text-white' : 'text-slate-300'}`}>
                                {item.label}
                            </span>
                            <span className="text-[10px] text-slate-500 leading-tight px-1">
                                {item.description}
                            </span>
                        </button>
                    );
                })}
            </div>
        </div>
    );
}
